import { fetchProducts } from "./productService.js";
import { fetchReviews } from "./reviewsService.js";
import { store as storeFile, fetchJson as fetchFile } from "./storageService.js";
import "./config.js";

const defaultContent = { products: [], reviews: [] }

export function renewContent(env) {
  const currentContent = getCurrentContent(env);
  const updatedProducts = fetchProducts(env);
  const updatedReviews = fetchReviews(env);
  const newContent = {
    products: updatedProducts ?? currentContent.products,
    reviews: updatedReviews ?? currentContent.reviews,
  }
  storeNewContent(newContent, env);
  invalidateCache();
}

function getCurrentContent(env) {
  const contentFileName = env.CONTENT_FILE_NAME;
  try {
    const currentContentFile = fetchFile(contentFileName, env);
    return currentContentFile == null ? defaultContent : JSON.parse(currentContentFile);
  } catch (e) {
    console.log("Error trying to get current content", e);
    return defaultContent;
  }
}

function storeNewContent(content, env) {
  const contentFileName = env.CONTENT_FILE_NAME;
  storeFile(contentFileName, JSON.stringify(content), env);
}

function invalidateCache() {

}
